import opg from 'ts/opg.ts';
import Panel from 'ts/ui/Panel.ts';
import Table from 'ts/ui/Table.ts';
import {Combo} from 'ts/ui/Combo';
import {Languages} from 'ts/util/Languages';
import {store} from '../../../ts/util/store';
import {request, string, dateTime, is, url, convert, format, array} from  'ts/util/utils';

console.log('Languages:', Languages);

opg.api({
    'getcateringlistByStandNo!post': 'Operation/GetcateringlistByStandNo',
});

const lpg = Languages.package;

let clng='en';

if(localStorage.getItem("lngPkg")!=null|| localStorage.getItem("lngPkg")!=undefined)
{
    const currentlng=localStorage.getItem("lngPkg");
    clng=currentlng.replace(/["""]/g,"");
}


let param = {};
param.reservetype=request["reservetype"];
param.currentlng=clng;
param.standno=request["standno"];
param.createtime=request["ctime"];


opg.api.getcateringlistByStandNo(param , (data)=>{
    for(var i=0;i<data.length;i++) {
        var _len = $("#listtable tr").length;
        $("#listtable").append("<tr align='center'>"
            +"<td id='td"+_len+"'>"+ data[i].reserveid+"</td>"
            +"<td>"+ data[i].reserveitemno+"</td>"
            +"<td>"+ data[i].itemname+"</td>"
            +"<td>"+ data[i].description+"</td>"
            +"<td>"+ data[i].oneday+"</td>"
            +"<td>"+ data[i].twoday+"</td>"
            +"<td>"+ data[i].threeday+"</td>"
            +"</tr>");
    }
});


window['getApprovalFill'] = function () {
    param.reserveid="";
    param.approvaltype="1";
    var len=$("#listtable tr").length;

    for(var i=1;i<len;i++)
    {
        param.reserveid=param.reserveid+ $("#td"+i).html() + ",";
    }

    return param;
};

window['getDenyFill'] = function () {
    param.reserveid="";
    param.approvaltype="2";
    var len=$("#listtable tr").length;

    for(var i=1;i<len;i++)
    {
        param.reserveid=param.reserveid+ $("#td"+i).html() + ",";
    }

    return param;
};

